import React, { Component } from "react";
import { Link } from "react-router-dom";
import Social from "../Social";
import posts from "../../fixtures";
import "./style.css";

export default class Share extends Component {
  render() {
    const url = window.location.href.replace('/share', '/modal');
    const encodeUrl = encodeURIComponent(url);
    const modalId = this.props.match.params.id;
    const post = posts.find(x => x.id === modalId);
    return (
      <div className="share-modal">
        <div className="share-modal__inner">
          <h2 className="share-modal__title">Share</h2>
          <div className="share-modal__body">
            <Social post={post} encodeUrl={encodeUrl}/>
            <input className="share-modal__field" value={url} readOnly/>
          </div>
          <Link className="share-modal__close" to="/">X</Link>
        </div>
      </div>
    );
  }
}